const Navbar = () => {
  return (
    <>
      <div className="navbar-container">
        <div className="navbar-btns">
          <ul className="navbar-btns-group">
            <Link to='/' className='home-navbar'> HOME 
              <bar className="home-nav-highlight"/>
            </Link>
            {/* <Link to='/systems' className='products-navbar'>SYSTEMS
              <bar className="products-nav-highlight"/>
              <Dropdown menuItems={productsMenu} menuType={'products-ddm'}
              conveyer={true}/>
            </Link>
            <Link to='/control' className='control-navbar'>CONTROL
              <bar className="control-nav-highlight"/>
              <Dropdown menuItems={controlMenu} menuType={'control-ddm'}/>
            </Link> */}
            <Link to='/products' className='products-navbar'>PRODUCTS
              <bar className="products-nav-highlight"/>
            </Link>
            <Link to='/services' className='services-navbar'>SERVICES
              <bar className="services-nav-highlight"/>
              {/* <Dropdown menuItems={servicesMenu} menuType={'services-ddm'}/> */}
            </Link>
            <Link to='/about' className='about-navbar'>ABOUT
              <bar className="about-nav-highlight"/>
            </Link>
            <Link to='/contact' className='contact-navbar'>CONTACT
              <bar className="contact-nav-highlight"/>
            </Link>
          </ul>
          {/* <Link to='/WIP-gotoexternallink' className="adsysapp-login">LOGIN</Link> */}
        </div>
      </div>
    </>
  )
}

const Dropdown = ({menuItems, menuType, conveyer}) => {
  return (
    <div className={`${menuType} dropdown-menu`}>
      <div className='dropdown-main-row'>
        {menuItems.map((item, index) => (
          <div key={index} className='dropdown-column'>
            <h3 className='dropdown-heading'>
              {item.heading}
            </h3>
            <ul className='dropdown-subheading-container'>
              {item.subHeading.map((item, index) => (
                <li key={index} className='dropdown-subheading'>
                  {item.item}
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>
      {conveyer === true ? <ConveyerSectionDropdown/> : null}
    </div>
  )
}

const ConveyerSectionDropdown = () => {
  return (
    <div className='dropdown-second-row'>
      {conveyerMenu.map((item, index) => (
        <div key={index} className='dropdown-conveyer-row'>
          <h3 className='dropdown-heading'>
            {item.heading}
          </h3>
          <ul className='dropdown-conveyer-subheading-container'>
            {item.subHeading.map((item, index) => (
              <li key={index} className='dropdown-conveyer-subheading'>
                {item.item}
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  )
}

export default Header